@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
    .main-container {
        width: 1440px;
        margin: 0 auto;
    }

    .h-main {
        height: calc(100vh - 4rem);
    }

    .bg-primary {
        background-color: #FB8C3B;
    }

    .bg-background {
        background-color: #EEEEEE;
    }

    .bg-menu {
        background-color: #707070;
    }

    .text-primary {
        color: #FB8C3B;
    }

    .text-context {
        color: #858585;
    }

    .text-menu {
        color: #707070;
    }

    .h-card {
        height: 400px;
    }
}
